import React from 'react'

const BlockInfo = (props) => {
    const {title, last, children} = props;

    return (
       
                <div className="block">
                    <div className="header">
                        {title}
                    </div>
                    <div className="content">
                    
                        {children}
                    </div>

                    <style jsx>
                {`  
                    .block {
                        background-color: #F4F6F6;
                        border: 1px solid #D3DDDD;
                        border-radius: 5px;
                        height: 282px;

                        margin-bottom: ${last ? 0 : 15}px;

                        color: #707070;
                        letter-spacing: 0;
                        
                        
                    }

                    .block .header {
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                        height: 60px;
                        border-bottom: 2px solid #FFFFFF;
                        font: Bold 16px/19px Arial;
                        letter-spacing: 0;
                        color: #000000;
                        padding: 0px 28px 0px 28px;
                    }

                    .block .content {
                        font: 10px/12px Arial;
                        letter-spacing: 0;
                        color: #000000;
                        padding: 0px 28px 0px 28px;
                        height: 282px;
                    }
                  
                `}

                </style>


                </div>

    )
}

export default BlockInfo;